import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import enTranslation from './locates/en/translation.json';
import viTranslation from './locates/vi/translation.json';

// Ti nguyn bn dch
const resources = {
  en: enTranslation,
  vi: viTranslation,
};

i18n
  .use(LanguageDetector) // Pht hin ngn ng trnh duyt
  .use(initReactI18next) // Kt ni vi React
  .init({
    resources,
    fallbackLng: 'en', // Ngn ng mc nh nu khng pht hin c
    interpolation: {
      escapeValue: false, // React  x l XSS
    },
  });

export default i18n;